<template>
  <div class="list">
    <h4 align="center">库存信息</h4>
    <table class="list-table">
      <tr>
        <th>序号</th>
        <th>名称</th>
        <th>入库数量</th>
        <th>库存总量</th>
        <th>库存余量</th>
        <th>更新时间</th>
        <th>操作</th>
      </tr>
      <tr v-if="!listData.length">
        <td class="no-data" colspan="7">暂无数据</td>
      </tr>
      <tr v-if="listData.length" v-for="(item,index) in listData" :key="index">
        <td>{{index+1}}</td>
        <td>{{item.name}}</td>
        <td>{{item.in}}</td>
        <td>{{item.stock}}</td>
        <td>{{item.balance}}</td>
        <td>{{item.update_at | formatDate}}</td>
        <td>
          <router-link class="btn btn-add" :to="'detail/'+item._id" tag="button">详情</router-link>
          <button class="btn btn-del" @click="setDelInfo(index, item._id)">删除</button>
        </td>
      </tr>
    </table>
    <div class="page-box" v-if="pagingnation.page_count > 1">
      <a class="page"
        :class="{'cur-page': pagingnation.page === index + 1}"
        v-for="(page,index) of pagingnation.page_count"
        :key="index"
        @click="getFruitByPage(index + 1)"
      >{{ index + 1 }}</a>
      <span class="page-count">共{{pagingnation.count}}条记录，每页显示
      <input type="text" :value="pagingnation.pre_page" class="pre-page" @keyup.enter="updatePage($event)">条</span>
    </div>
    <my-modal :show.sync="showModal" :delIndex="delData.index" @confirm="delFruit"></my-modal>
  </div>
</template>
<script>
import MyModal from '@/components/MyModal';
export default {
  data() {
    return {
      delData: {
        index: null,
        id: null
      },
      showModal: false,
      pagingnation: {
        count: 0,
        page: 1,
        page_count: 1,
        pre_page: 10
      }
    }
  },
  created () {
    this.getFruitByPage(1)
  },
  computed: {
    listData() {
      return this.$store.state.fruitData
    }
  },
  methods: {
    detail(index) {
      this.$router.push(`/detail?id=${index}`)
    },
    getFruitByPage(page) {
      let pageInfo = {
        page: page,
        pre_page: this.pagingnation.pre_page
      }
      this.$store.dispatch('getFruitData', pageInfo).then(pagingnation => {
        // console.log(pagingnation);
        this.pagingnation = pagingnation
      })
    },
    updatePage(event) {
      this.pagingnation.pre_page = event.target.value
      this.getFruitByPage(1)
    },
    setDelInfo(index, id) {
      this.delData.index = index
      this.delData.id = id
      this.showModal = true
    },
    delFruit(){
      this.$store.dispatch('delFruitData',this.delData.id).then(res => {
        this.showModal = false
        this.getFruitByPage(this.pagingnation.page)
      })
    }
  },
  components: {
    MyModal
  }
}
</script>

<style lang="less" scoped>
//表格
.list-table {
  margin: 25px auto 15px;
  width: 100%;
  border-collapse: collapse;
  th {
    background: #bbbec4;
    font-size: 16px;
    text-align: center;
    line-height: 30px;
    color: #fff;
    border: 1px solid #bbbec4;
  }
  td {
    border: 1px solid #bbbec4;
    padding: 5px;
    text-align: center;
    font-size: 16px;
    color: #282b32;
  }
  .no-data{
    color: #DDDEE1;
    padding: 10px 0;
  }
}
.page-box{
  text-align: left;
  .page{
    display: inline-block;
    font-size: 16px;
    border: 1px solid #5CADFF;
    width: 20px;
    line-height: 20px;
    margin: 0 3px;
    cursor: pointer;
    border-radius: 2px;
    text-align: center;
    &.cur-page{
      background-color: #5CADFF;
      color: #fff;
      cursor: default;
    }
  }
  .page-count{
    float:right;
    color: #80848F;
    .pre-page{
      width: 30px;
      margin: 0 5px;
      text-align: center;
      color: #80848f;
      border-radius: 1px;
      border: 1px solid #80848F;
    }
  }
}
</style>



